.App {
  text-align: center;
  min-height: 100vh;
  overflow: hidden;
  display: block;
  position: relative;
  padding-bottom: 200px; }

.imgStyle {
  width: 100%;
  height: auto;
  margin-bottom: 3vw; }

.hStyle {
  text-align: left;
  margin: 3vw 0 2vh 4vw; }

.footer {
  background-color: #212529;
  position: absolute;
  bottom: 0;
  width: 100%; }

.col {
  padding: 5vh 0 5vh 0;
  color: white;
  max-width: 100%;
  text-align: center; }

a:link {
  color: #808080; }

a:visited {
  color: #A9A9A9; }

a:hover {
  color: #C0C0C0; }

.justified {
  text-align: justify; }

.marginTB {
  margin: 5vh 0 15vh 0; }

.background {
  background: url(/booxin/static/media/bg.242f9200.png);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: 100vh; }

.search {
  position: relative;
  top: 60vh; }

.leftMargin {
  margin-left: 5px; }

.wrapper {
  display: flex;
  max-width: 80%; }

.box {
  min-width: 20vw;
  min-height: 10vh; }

.box-info {
  min-width: 50vw;
  min-height: 10vh; }

.cover {
  display: block;
  justify-content: center;
  max-width: 120px;
  max-height: 178px; }

.info {
  justify-content: center; }

.align {
  justify-content: center;
  margin: 5px; }

.button {
  margin: 5px; }

.pagination {
  display: inline-block;
  margin-top: 10vh;
  justify-content: center; }

.pagination a {
  color: black;
  float: left;
  padding: 8px 16px;
  text-decoration: none; }

.align-left {
  display: block;
  text-align: left;
  margin: 2vw; }

